import * as React from "react";
import { render } from "react-dom";
import { AppContainer } from "react-hot-loader";
import { Provider } from "react-redux";
import { storeCreator } from "./store/createStore";
import { AuthenticationContainer } from "./components/authentication/components-authentication-container";
const rootEl = document.getElementById("root");

const store = storeCreator();
render(
    <Provider store={store}>
        <AppContainer>
            <AuthenticationContainer />
        </AppContainer>
    </Provider>,
    rootEl
);

// Hot Module Replacement API
declare let module: { hot: any };

if (module.hot) {
    module.hot.accept("./components/authentication/components-authentication-container", () => {
        const NewApp = require("./components/authentication/components-authentication-container").default;

        render(
            <AppContainer>
                <NewApp />
            </AppContainer>,
            rootEl
        );
    });
}
